.topBar{
    position: sticky;
    width: 100%;
    background: white;
    height: 50px;
    top: 0;
    z-index: 999;
}
.topBarWrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;
    padding: 0 20px;

}
.logo{
    font-weight: bold;
    font-size: 30px;
    color: darkblue;
    cursor: pointer;
}
.topRight{
    display: flex;
    justify-content: center;
    align-items: center;

}
.topIconContainer{
    position: relative;
    margin-right: 10px;
    cursor: pointer;
    color: #555;
}
.topIconBadge{
    position: absolute;
    right: 0;
    top: -5px;
    border-radius: 50%;
    background: red;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 15px;
    height: 15px;
    font-size: 10px;
}

.topAvatar{
    width: 40px;
    height: 40px;
    border-radius: 50%;
}