.sidebar{
    position: sticky;
    top: 50px;
    height: calc(100vh - 50px);
    background: rgb(251,251,255);
    flex: 1;

}
.sidebarWrapper{
    padding: 20px;

}
.sidebarMenu{
    margin-bottom: 20px;
    color: #555555;
}
.sidebarTitle{
    font-weight: bold;
    font-size: 13px;
    color: rgb(187,186,186);
    flex: 1;

}
.sidebarList{
    list-style: none;
    padding:5px;
}
.sidebarListItems{
    display: flex;
    align-items: center;
    border-radius: 10px;
    padding: 5px;
    cursor: pointer;
    background: inherit;
}

.active , .sidebarListItems:hover{
    background: rgb(240,240,255);
}
.sidebarIcon{
    margin-right: 5px;

}