.userList{
    flex: 4;
}

.userListUser{
    display: flex;
    align-items: center;
}

.userListImg{
    width: 32px;
    height: 32px;
    margin-right: 10px;
    object-fit: cover;
}
.userListEdit{
    color: #fff;
    border: none;
    background-color: #3bb077;
    border-radius: 10px;
    cursor: pointer;
    padding: 5px 7px ;
    margin-right: 20px;
}
.userListDelete{
    color: red;
    cursor: pointer;
}