.features{
    width: 100%;
    display: flex;
    justify-content: space-between;

}
.featuresItem{
    cursor: pointer;
    padding: 30px;
    margin: 0 20px;
    border-radius: 10px;
    box-shadow: 0 0 15px -10px rgba(0,0,0,0.75);
    flex: 1;
}
.featuresTitle{
    font-size: 20px;
    font-weight: bold;
}
.featuresContainer{
    display: flex;
    align-items: center;
    margin: 10px 0;


}
.featuresMoney{
    font-weight: 600;
    font-size: 30px;
}
.featuresRate{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 20px;
}
.featuresIcon{
    color: green;
    font-size: 24px;
    margin-left: 5px;

}
.featuresIcon.negative{
    color: red;
}
.featuresSub{
    font-size: 15px;
    color: gray;
}