.product {
    flex: 4;
    padding: 20px;
}

.productTitleContainer {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.productAddButton {
    border: none;
    border-radius: 5px;
    color: white;
    cursor: pointer;
    background-color: teal;
    padding: 5px;
    font-size: 16px;
}

.productTop {
    display: flex;
}

.productTopLeft,
.productTopRight {
    flex: 1;
}

.productTopRight {
    margin: 20px;
    padding: 20px;
    box-shadow: 0 0 15px -10px rgba(0, 0, 0, 0.75);
}
.productInfoTop{
    display: flex;
    align-items: center;
}
.productInfoImg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-right: 20px;
}
.productInfoName{
    font-weight: 600;
}
.productInfoBottom{
    margin-top: 20px;
}
.productInfoItem{
    width: 200px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.productInfoKey{
    font-weight: 600;
}
.productInfoValue{
    font-weight: 300;
}
.productBottom{
    padding: 20px;
    margin: 20px;
    box-shadow: 0 0 15px -10px rgba(0, 0, 0, 0.75);

}
.productForm{
    display: flex;
    justify-content: space-between;
}
.productFormLeft{
    display: flex;
    flex-direction: column;

}
.productFormLeft label{
    color: gray;
    margin-bottom: 10px;
}
.productFormLeft input{
    border: none;
    padding: 5px;
    border-bottom:1px solid gray ;
    margin-bottom: 10px;
    outline: none;
}
.productFormLeft select{
    margin-bottom: 10px;
}
.productFormRight{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.productUploader{
    display: flex;
    align-items: center;
}
.productUploaderImg{
    width: 100px;
    height: 100px;
    border-radius: 10px;
    margin-right: 20px;
}
.productButton{
    background-color: darkblue;
    color: white;
    font-weight: 600;
    cursor: pointer;
    padding: 5px;
    border: none;
    border-radius: 5px;
}